@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');


.App{
  text-align: center;
}

*{
  margin: 0;
  font-family: "Roboto";
  color: #333;

  .navbar{
    padding: 20px;
    display: flex;
    align-items: center;
    margin: 0 auto;
    border-bottom: 2px solid #f2f2f2;
  }

  .navbar h1{
    color: #f1356d;
  }

  .navbar .links{
    margin-left: auto;
  }

  .links a{
    color: #f1356d;
  }

  .navbar a{
    margin-left: 16px;
    text-decoration: none;
    padding: 6px;
  }

  .navbar a:hover{
    color: #f1356d;
  }

  .content{
    max-width: 600px;
    margin: 40px auto;
    padding: 20px;
  }
}

/* blog previews / list */

.blog-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
}

.blog-preview:hover {
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}

.blog-preview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
  text-decoration: none;
}

.blog-preview a{
  text-decoration: none;
}

/* the usingaxios jokes app api*/
.jokes{
  text-align: center;
  font-family: "Quicksand";
  
  border-bottom: 2px #333;
  padding: 30px;
  box-shadow: #333;
}
.jokes h3{
  color: #f1356d;
}

.jokes h3:hover{
  color:brown;
}

.contact h2{
  color: #f1356d;
}